import './styles/main.scss';
import { HawkeyeCore } from './core/HawkeyeCore';
import { DefaultConfig } from './config/defaultConfig';

class HawkeyeOverlayTool {
  constructor(publishingContainerId = 'publishingContainer') {
    const config = {
      ...DefaultConfig,
      ui: {
        ...DefaultConfig.ui,
        containerId: publishingContainerId
      }
    };

    this.core = new HawkeyeCore(config);
    this.initialize();
  }

  async initialize() {
    try {
      await this.core.initialize();
    } catch (error) {
      console.error('Failed to initialize Hawkeye:', error);
    }
  }

  destroy() {
    if (this.core) {
      this.core.destroy();
    }
  }

  static initialize(publishingContainerId = 'publishingContainer') {
    return new HawkeyeOverlayTool(publishingContainerId);
  }
}

// 전역 객체로 노출
window.HawkeyeOverlayTool = HawkeyeOverlayTool;

// 자동 초기화
if (document.readyState === 'loading') {
  document.addEventListener('DOMContentLoaded', () => HawkeyeOverlayTool.initialize());
} else {
  HawkeyeOverlayTool.initialize();
}

export default HawkeyeOverlayTool;